export default {
    name: "Archive",
    props: {
        value: {},
        response: {},
        query: {}
    },
    computed: {
        totalPage() {
            return this.response.count / (this.query['page_size'] ? this.query['page_size'] : 10)
        },
        next() {
            if (this.query.page < this.totalPage) {
                return this.query.page + 1;
            }
            return 0
        },
        previous() {
            if (this.query.page > 1) {
                return this.query.page - 1;
            }
            return 0
        }
    }
}
