import Avatar from "../partials/Avatar";
import User from "../partials/User";

export default {
    name: "Content",
    components: {User, Avatar},
    props: {
        value: {},
        description: {
            default: false,
            type: Boolean
        },
        vote: {
            default: false,
            type: Boolean
        },
        media: {
            default: false,
            type: Boolean
        },
        hashTag: {
            default: false,
            type: Boolean
        },
        meta: {
            default: false,
            type: Boolean
        },
    },
    computed: {
        taxPrimaries() {
            if (this.hashTag) {
                return this.value['post_terms'].filter(x => x.taxonomy === 'object');
            }
            return []
        }
    }
}
