export default {
    name: "Avatar",
    props: {
        canUpdate: {
            type: Boolean,
            default: false
        },
        value: {
            default: null,
            type: Object
        },
        name: {
            default: null
        },
        icon: {
            default: 'account-circle-outline',
            type: String
        }
    },
    data() {
        return {
            data: this.value,
            isUpdating: false
        }
    },
    methods: {
        handleUpload(files) {
            if (files.length) {
                this.data = files[0]
                this.isUpdating = false
            }
        }
    },
    watch: {
        data() {
            this.$emit('input', this.data)
        },
        value() {
            this.data = this.value
        }
    }
}
