export default {
    name: "Index",
    props: {
        resTaxonomy: {},
        resPost: {},
        query: {}
    },
    computed: {
        totalPage() {
            return this.resPost.count / (this.query.page_size ? this.query.page_size : 10)
        },
        next() {
            if (this.query.page < this.totalPage) {
                return this.query.page + 1;
            }
            return 0
        },
        previous() {
            if (this.query.page > 1) {
                return this.query.page - 1;
            }
            return 0
        }
    }
}
